
.loading { 
    position: fixed; 
    z-index: 9999;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .loading .loading-mask {
    position:  absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $white; 
    opacity: 0.5;
  }
  
  .loading .loading-img {
    position:  absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  
  @keyframes ldio-fhsto9ag0lp {
    0% {
      top: 96px;
      left: 96px;
      width: 0;
      height: 0;
      opacity: 1;
    }
  
    100% {
      top: 18px;
      left: 18px;
      width: 156px;
      height: 156px;
      opacity: 0;
    }
  }
  
  .ldio-fhsto9ag0lp div {
    position: absolute;
    border-width: 4px;
    border-style: solid;
    opacity: 1;
    border-radius: 50%;
    animation: ldio-fhsto9ag0lp 1s cubic-bezier(0,0.2,0.8,1) infinite;
  }
  
  .ldio-fhsto9ag0lp div:nth-child(1) {
    border-color: #0ce937;
    animation-delay: 0s;
  }
  
  .ldio-fhsto9ag0lp div:nth-child(2) {
    border-color: #46f056;
    animation-delay: -0.5s;
  }
  
  .loadingio-spinner-ripple-ett975kzlg6 {
    width: 200px;
    height: 200px;
    display: inline-block;
    overflow: hidden;
    background: none;
  }
  
  .ldio-fhsto9ag0lp {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0; /* see note above */
  }
  
  .ldio-fhsto9ag0lp div { 
    box-sizing: content-box; 
  }
  
  